@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

button {
    cursor: pointer;
}

a {
    text-decoration: none;
    color: inherit;
}

li,
ul {
    list-style: none;
}

.d-none {
    display: none;
}

.App {
    overflow-x: hidden;
}

.container {
    max-width: 1600px;
    width: 90%;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.col-6 {
    width: 50%;
    padding: 0 15px;
}

.col-4 {
    width: 33.3%;
    padding: 0 15px;
}
.col-3 {
    width: 25%;
    padding: 0 15px;
}

